#works {
  margin-top: 5em;
  h1 {
    font-style: italic;
    font-weight: 800;
    font-size: 2rem;
    line-height: 37px;
    color: #000000;
    margin-bottom: 3em;
  }

  h4 {
    font-style: italic;
    font-weight: 800;
    font-size: 1rem;
    line-height: 20px;
    color: #000000;
    margin-bottom: 3em;
  }
  .carousel-img {
    display: flex;
    object-fit: contain;
    padding: 0 0.4em;
    width: 80%;
  }
  .works-link {
    text-decoration: none;
    color: #000000;
  }
}
