#first-section {
  margin: 11em 0 4em 0;
  width: 100%;
  .feed-links {
    margin-bottom: 3em;
    a {
      text-decoration: none;
      color: #000;
      font-size: 0.8rem;
      margin-right: 4em;
      font-style: italic;
    }
  }
  .postImg {
    width: 93%;
  }

  .para-sec {
    h1 {
      font-weight: 800;
      font-size: 2.5rem;
      line-height: 49px;
      letter-spacing: -2px;
      text-transform: uppercase;
      font-style: italic;
      margin-bottom: 0.3em;
    }
    p {
      font-style: normal;
      font-weight: normal;
      font-size: 15px;
      line-height: 19px;
      margin-bottom: 3em;
    }
    h4 {
      font-style: italic;
      font-weight: 800;
      font-size: 15px;
      line-height: 30px;
      margin-bottom: 1em;
    }
    .info {
      h4 {
        font-style: italic;
        font-weight: 800;
        font-size: 15px;
        line-height: 30px;
        margin-bottom: 1em;
      }
      p {
        font-style: normal;
        font-weight: normal;
        font-size: 15px;
        line-height: 19px;
        margin-bottom: 3em;
      }
    }

    .time-price-sec {
      .linear-color {
        font-family: "Oswald", sans-serif;
        font-style: normal;
        font-weight: 700;
        font-size: 2.5rem;
        line-height: 48px;
        margin-bottom: 2rem;
        background: linear-gradient(
          91.26deg,
          #8ad4ec 0%,
          #ef96ff 21.74%,
          #ff56a9 54.03%,
          #ffaa6c 85.28%
        );

        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        /* or 140% */

        font-feature-settings: "pnum" on, "lnum" on;
      }
      .count-class {
        font-family: "Oswald", sans-serif;
        font-style: normal;
        font-weight: 600;
        font-size: 2.5rem;
        line-height: 48px;
        text-align: center;
        color: #000;
        margin-bottom: 2rem;
      }
      .price-details {
        margin-top: 2rem;
      }
      .price-details span {
        font-family: "Oswald", sans-serif;
        font-style: normal;
        font-weight: bold;
        font-size: 0.9rem;
        line-height: 18px;
        text-align: center;

        color: #000000;
      }
      .count-details {
        margin-top: 2rem;
      }
      .count-details span {
        font-family: "Oswald", sans-serif;
        font-style: normal;
        font-weight: bold;
        font-size: 0.9rem;
        line-height: 18px;
        text-align: center;
        margin: 0.83rem;
        color: #000000;
      }
      .para-btn {
        margin-top: 4em;
        display: block;
        padding: 1.5em 1em;
        width: 90%;
      }
    }
  }
}
