#footer {
  background: #000;
  color: #fff;
  width: 100%;
  padding: 3em 5em 4em 5em;
  margin-top: 4em;
  .top-title {
    padding: 4em 0;
  }
  .top-title-1 {
    font-style: normal;
    font-family: "Oswald", sans-serif;
    font-weight: 400;
    font-size: 4.5rem;
    line-height: 96px;
    letter-spacing: -0.03em;

    color: #ffffff;
  }
  .top-title-2 {
    font-style: normal;
    font-family: "Oswald", sans-serif;
    font-weight: 300;
    font-size: 4.5rem;
    line-height: 96px;
    letter-spacing: -0.03em;
    color: #ffffff;
  }
  .footer-sec-section {
    h3 {
      font-style: normal;
      font-weight: normal;
      font-size: 1.3rem;
      line-height: 31px;
      letter-spacing: -0.02em;
    }
    p {
      font-style: normal;
      font-weight: normal;
      font-size: 0.9rem;
      line-height: 17px;

      color: #ffffff;
    }
    p span {
      display: block;
    }
  }
  .footer-social {
    display: flex;
    align-items: self-end;
    justify-content: space-evenly;
    margin-bottom: 4em;
  }
  .footer-social a {
    text-decoration: none;
    color: #fff;
  }
}

@media screen and (max-width: 768px) {
  #footer .second-footer {
    margin-top: 5em;
  }
}
