/* Keyframes */
@-webkit-keyframes circle {
  0% {
    width: 1px;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    height: 1px;
    z-index: -1;
    background: #eee;
    border-radius: 100%;
  }
  100% {
    background: #aaa;
    height: 5000%;
    width: 5000%;
    z-index: -1;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    border-radius: 0;
  }
}
.links a {
  text-decoration: none;
  display: inline-block;
  padding: 6px 2em;
  font-style: normal;
  font-weight: normal;
  color: #000;
  font-size: 16px;
  line-height: 24px;
  position: relative;
  overflow: hidden;
  z-index: 1;
  transition: all 0.5s;
  border-top: 4px solid #ffffff00;
  border-bottom: 4px solid #ffffff00;
}
.links a:hover {
  border-top: 4px solid #000;
  border-bottom: 4px solid #000;
  padding: 6px 2em;
}

.nav-new {
  background: #ffffffba !important;
  color: #000 !important;
  padding: 1em 2em;
  box-shadow: none !important;

  #bar-shadow {
    box-shadow: none !important;
  }
  .avatar {
    border-radius: 0 !important;
    width: auto !important;
    height: auto !important;
    margin-bottom: -1rem;
  }
}
#search-input {
  display: none !important;
}
.flex-class {
  display: flex;
}
@media screen and (max-width: 768px) {
  .links {
    display: none;
  }
}
