#artist {
  h1 {
    font-style: italic;
    font-weight: 800;
    font-size: 2rem;
    line-height: 37px;
    color: #000000;
    margin-bottom: 2em;
  }
  .created-art {
    font-style: normal;
    font-weight: 300;
    font-size: 1rem;
    line-height: 30px;
    color: #000000;
  }
  .date-created {
    font-style: normal;
    font-weight: 300;
    font-size: 0.85rem;
    line-height: 30px;
    color: #000000;
  }
  .artist-photo {
    display: flex;
    margin-bottom: 2em;
    div {
      margin-left: 2.5em;
    }
  }
  .artist-socials {
    a {
      text-decoration: none;
      color: #000000;
      display: flex;
      align-items: center;

      .icon-class {
        margin-right: 1em;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  #artist .date-created {
    margin-bottom: 5em;
  }
}
